.promotion-box-2 {
    position: absolute;
    top: 50%;
    left: 70vw;
    height: 50vh;
    width: 300vw;
    background-color: #555;
    display: none;
    animation: moveFromUpHalf 1s forwards;
    animation-iteration-count: 1;
    
    background-image: url(/react-webapp-shtav/static/media/img1.a91a0a72.jpg);
